import { Link } from 'react-router-dom';

const PageNotFound = () => {
    return (
        <div style={{ textAlign: 'center' }}>
            <h1> 404 </h1>
            <hr />
            <h2> Page not found </h2>
                <Link to="/">
                    <h4>
                        Go to Home
                    </h4>
                </Link>
        </div>
    );
};

export default PageNotFound;
