import { useParams, useNavigate, Link } from 'react-router-dom';
import { useEffect, useState } from "react";
import Navigation from '../Navigation';
import {serverPath} from '../helpers/variable'
import Form from './Form';
import { ClassicSpinner } from 'react-spinners-kit';


const Edit = () => {
    const [request, setRequest] = useState(null);
    const [loading, setLoading] = useState(true);
    const { id } = useParams();
    const navigate = useNavigate();

    useEffect(() => {
        fetch(serverPath + 'requests/' + id)
            .then((res) => {
                return res.json();
            })
            .then((data) => {
                setRequest({
                    ...data,
                    dateDate: new Date(data.date).toLocaleDateString(),
                    dateTime: new Date(data.date).toLocaleTimeString(),
                });
                setLoading(false);
            })
            .catch((error) => {
                console.log(error.message);
            });
    }, []);

    const changeInfo = (e) => {
        setRequest((request) => {
            return {
                ...request,
                [e.target.name]: e.target.value,
            };
        });
    };

    const changeRequest = (e) => {
        e.preventDefault();
        fetch(serverPath + 'requests/' + id, {
            method: 'PUT',
            headers: { 'Content-type': 'application/json' },
            body: JSON.stringify(request),
        })
        .then(() => navigate('/table'))
        .catch((error) => {
            console.log(error.message);
        });
            
    }

    const deleteRequest = (e) => {
        e.preventDefault();
        fetch(serverPath + 'requests/' + id, {
            method: 'DELETE',
        }).then(() => navigate('/table'));
    };

    return (
        <div className="with-nav">
            <Navigation />
            <div className="form-wrapper">
                <div className="container-fluid">
                    <div className="row justify-content-between align-items-center">
                        <div className="col">
                            <div className="admin-heading-1">Работа с заявкой</div>
                        </div>
                        <div className="col text-right">
                            <Link to="/table" className="btn btn-link">
                                Вернуться назад
                            </Link>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col">
                            <ClassicSpinner size={50} color="#686769" loading={loading} />
                            {request && (
                                <Form
                                    request={request}
                                    changeInfo={changeInfo}
                                    changeRequest={changeRequest}
                                    deleteRequest={deleteRequest}
                                />
                            )}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
 
export default Edit;