const TableLeftPanel = ({ changeStatus, filterStatus, countNewRequests }) => {
    return (
        <div className="left-panel blue-skin">
            <div className="left-panel__logo">
                <div className="left-panel__logo-title">CRM заявки</div>
                <div className="left-panel__logo-subtitle">учебный проект webcademy</div>
            </div>
            <div className="left-panel__user clearfix">
                <div className="left-panel__user-photo">
                    <img src='./avatar-128.jpg' alt="Avatar" />
                </div>
                <div className="left-panel__user-name">
                    Петр <br />
                    Васильевич
                </div>
            </div>
            <div className="left-panel__navigation">
                <div className="left-panel__navigation-title">Заявки</div>
                <ul>
                    <li>
                        <a
                            onClick={changeStatus}
                            data-value="all"
                            data-role="left-status"
                            href="#"
                            className={filterStatus === 'all' ? 'active' : ''}
                        >
                            Все вместе
                        </a>
                    </li>
                    <li>
                        <a
                            onClick={changeStatus}
                            data-value="new"
                            data-role="left-status"
                            href="#"
                            className={filterStatus === 'new' ? 'active' : ''}
                        >
                            Новые
                            <div className="badge" id="badge-new">
                                {countNewRequests}
                            </div>
                        </a>
                    </li>
                    <li>
                        <a
                            onClick={changeStatus}
                            data-value="inwork"
                            data-role="left-status"
                            href="#"
                            className={filterStatus === 'inwork' ? 'active' : ''}
                        >
                            В работе
                        </a>
                    </li>
                    <li>
                        <a
                            onClick={changeStatus}
                            data-value="complete"
                            data-role="left-status"
                            href="#"
                            className={filterStatus === 'complete' ? 'active' : ''}
                        >
                            Завершенные
                        </a>
                    </li>
                </ul>
            </div>
        </div>
    );
};
 
export default TableLeftPanel;