import { useState, useEffect } from "react";
import TableList from "./TableList"
import Navigation from '../Navigation';
import TableLeftPanel from "./TableLeftPanel";
import TableStatus from "./TableStatus";
import { serverPath } from '../helpers/variable';
import { ClassicSpinner } from 'react-spinners-kit';

const Table = () => {
    const [requests, setRequests] = useState([]);
    const [updateflag, setUpdateFlag] = useState(false);
    const [filterProduct, setFilterProduct] = useState('all');
    const [filterStatus, setFilterStatus] = useState('all');
    const [loading, setLoading] = useState(true);
    let filterByProduct, filterByStatus;

    useEffect(() => {
        document.body.className = 'with-nav';
        const controller = new AbortController();

        fetch(serverPath + 'requests', { signal: controller.signal })
            .then((res) => {
                if (res.ok !== true) {
                    throw Error('Could not fetch the data from this resource');
                }
                return res.json();
            })
            .then((data) => {
                setRequests(data);
                setLoading(false)
            })
            .catch((err) => {
                if (err.name === 'AbortError') {
                    console.log('Fetch aborted');
                } else {
                    console.log(err);
                }
            });
        
        return () => {
            controller.abort();
        };
    }, [updateflag]);

    if (filterProduct === 'all') {
        filterByProduct = requests;
    } else {
        filterByProduct = requests.filter((request) => {
            return request.product === filterProduct;
        });
    }

    if (filterStatus === 'all') {
        filterByStatus = filterByProduct;
    } else {
        filterByStatus = filterByProduct.filter((request) => {
            return request.status === filterStatus;
        });
    }

    const chooseProduct = (e) => {
        setFilterProduct(e.target.value);
    };

    const changeStatus = (e) => {
        e.preventDefault();
        setFilterStatus(e.target.dataset.value);
    };

    const newRequests = requests && requests.filter((request) => {
        return request.status === 'new'
    });

    return (
        <div className="body--dashboard">
            <Navigation />
            <TableLeftPanel
                changeStatus={changeStatus}
                filterStatus={filterStatus}
                countNewRequests={newRequests.length}
            />
            <div className="main-wrapper">
                <div className="container-fluid">
                    <div className="admin-heading-1">Все заявки</div>
                    <TableStatus
                        chooseProduct={chooseProduct}
                        changeStatus={changeStatus}
                        filterStatus={filterStatus}
                    />
                    {requests && (
                        <TableList requests={filterByStatus} setUpdateFlag={setUpdateFlag} updateflag={updateflag} />
                    )}
                    <ClassicSpinner size={50} color="#686769" loading={loading} />
                </div>
            </div>
        </div>
    );
}
 
export default Table;