import { useState } from 'react'
import Navigation from '../Navigation';
import randomData from '../../randomData/data'
import { serverPath } from '../helpers/variable';


const Form = () => {
    const [data, setData] = useState(randomData());
    const [isPending, setIsPending] = useState(false);

    const handleSubmit = (e) => {
        e.preventDefault();
        const request = {
            ...data,
            date: new Date().toISOString(),
            status: 'new',
        };
        setIsPending(true);
        fetch(serverPath + 'requests', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(request),
        }).then(() => {
            console.log('New request was added');
            setIsPending(false);
            setData(randomData());
        });
    };

    const changeInfo = (e) => {
        setData((request) => {
            return {
                ...request,
                [e.target.name]: e.target.value,
            };
        });
    };

    return (
        <div className="with-nav radial-bg flex-center">
            <Navigation />
            <div className="white-plate white-plate--payment">
                <div className="container-fluid">
                    <div className="white-plate__header text-center">
                        <p className="white-plate__logo">
                            <span>Форма</span> заявок
                        </p>
                    </div>
                    <div className="white-plate__line-between white-plate__line-between--main"></div>
                    <form onSubmit={handleSubmit} id="form" method="POST" action="">
                        <label>Ваши данные:</label>
                        <div className="form-group">
                            <input
                                id="name"
                                type="text"
                                name="name"
                                autoComplete="on"
                                className="form-control"
                                placeholder="Имя и Фамилия"
                                required
                                value={data.name}
                                onChange={changeInfo}
                            />
                        </div>
                        <div className="form-group">
                            <input
                                id="phone"
                                type="text"
                                name="phone"
                                autoComplete="on"
                                className="form-control"
                                placeholder="Телефон"
                                value={data.phone}
                                onChange={changeInfo}
                            />
                        </div>
                        <div className="form-group">
                            <input
                                id="email"
                                type="email"
                                name="email"
                                autoComplete="on"
                                className="form-control"
                                placeholder="Email"
                                required
                                value={data.email}
                                onChange={changeInfo}
                            />
                        </div>
                        <div className="form-group">
                            <label htmlFor="exampleFormControlSelect1">Продукт:</label>
                            <select
                                value={data.product}
                                onChange={changeInfo}
                                id="product"
                                name="product"
                                className="form-control"
                            >
                                <option value="product-html">Курс по верстке</option>
                                <option value="product-js">Курс по JavaScript</option>
                                <option value="product-vue">Курс по VUE JS</option>
                                <option value="product-php">Курс по PHP</option>
                                <option value="product-wordpress">Курс по WordPress</option>
                            </select>
                        </div>
                        <div className="form-group">
                            {isPending && (
                                <button className="btn btn-lg btn-primary btn-block" disabled>
                                    Заявка оформляется
                                </button>
                            )}
                            {!isPending && (
                                <button type="submit" className="btn btn-lg btn-primary btn-block">
                                    Оформить заявку
                                </button>
                            )}
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
}
 
export default Form;