import { Link } from 'react-router-dom';

const TableListItem = ({ request }) => {
    const { name, email, phone, product, id, date } = request;

    const products = {
        'product-html': 'Курс по верстке',
        'product-js': 'Курс по JS',
        'product-vue': 'Курс по Vue',
        'product-php': 'Курс по PHP',
        'product-wordpress': 'Курс по Wordpress',
    };

    const statuses = {
        new: 'Новая',
        inwork: 'В работе',
        complete: 'Завершена',
    };

    const badges = {
        new: 'badge-danger',
        inwork: 'badge-warning',
        complete: 'badge-success',
    };

    let localDate = new Date(date).toLocaleDateString();

    return (
        <tr>
            <th scope="row">{id}</th>
            <td>{localDate}</td>
            <td>{products[product]}</td>
            <td>{name}</td>
            <td>{email}</td>
            <td>{phone}</td>
            <td>
                <div className={`badge badge-pill ${badges[request.status]}`}>{statuses[request.status]}</div>
            </td>
            <td>
                <Link to={`/edit/${id}`}>Редактировать</Link>
            </td>
        </tr>
    );
};

export default TableListItem;
