const Form = ({ request, changeInfo, changeRequest, deleteRequest }) => {
    return (
        <form id="form">
            <div className="card mb-4">
                <div className="card-header">Данные о заявке</div>
                <div className="card-body">
                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>ID:</strong>
                        </div>
                        <div className="col">
                            Заявка №<span id="number">{request.id}</span>
                        </div>
                    </div>
                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>Дата создания:</strong>
                        </div>
                        <div className="col" id="date">
                            {request.dateDate}:{request.dateTime}
                        </div>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>Продукт:</strong>
                        </div>
                        <div className="col">
                            <select
                                id="product"
                                name="product"
                                className="custom-select"
                                value={request.product}
                                onChange={changeInfo}
                            >
                                <option value="product-html">Курс по верстке</option>
                                <option value="product-js">Курс по JavaScript</option>
                                <option value="product-vue">Курс по VUE JS</option>
                                <option value="product-php">Курс по PHP</option>
                                <option value="product-wordpress">Курс по WordPress</option>
                            </select>
                        </div>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>Имя:</strong>
                        </div>
                        <div className="col">
                            <input
                                type="text"
                                className="form-control"
                                value={request.name}
                                onChange={changeInfo}
                                id="name"
                                name="name"
                            />
                        </div>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>Email:</strong>
                        </div>
                        <div className="col">
                            <input
                                type="text"
                                className="form-control"
                                value={request.email}
                                onChange={changeInfo}
                                id="email"
                                name="email"
                            />
                        </div>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>Телефон:</strong>
                        </div>
                        <div className="col">
                            <input
                                type="text"
                                className="form-control"
                                value={request.phone}
                                onChange={changeInfo}
                                id="phone"
                                name="phone"
                            />
                        </div>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-2">
                            <strong>Статус заявки:</strong>
                        </div>
                        <div className="col">
                            <select
                                value={request.status}
                                onChange={changeInfo}
                                className="custom-select"
                                id="status"
                                name="status"
                            >
                                <option>Выберите...</option>
                                <option value="new">Новая</option>
                                <option value="inwork">В работе</option>
                                <option value="complete">Завершена</option>
                            </select>
                        </div>
                    </div>
                </div>
            </div>
            <div className="row justify-content-between">
                <div className="col text-right">
                    <button onClick={changeRequest} type="submit" className="btn btn-primary">
                        Сохранить изменения
                    </button>
                    <button onClick={deleteRequest} type="submit" className="btn btn-danger ml-2">
                        Удалить
                    </button>
                </div>
            </div>
        </form>
    );
};
 
export default Form;