const TableStatus = ({ chooseProduct, changeStatus, filterStatus }) => {
    return (
        <form action="">
            <div className="form-row">
                <div className="row mb-3 justify-content-start">
                    <div className="col">
                        <div id="topStatusBar" className="btn-group" role="group" aria-label="...">
                            <a
                                onClick={changeStatus}
                                href="#"
                                className={`btn btn-light ${filterStatus === 'all' ? 'active' : ''}`}
                                data-value="all"
                            >
                                Все
                            </a>
                            <a
                                onClick={changeStatus}
                                href="#"
                                className={`btn btn-light ${filterStatus === 'new' ? 'active' : ''}`}
                                data-value="new"
                            >
                                Новые
                            </a>
                            <a
                                onClick={changeStatus}
                                href="#"
                                className={`btn btn-light ${filterStatus === 'inwork' ? 'active' : ''}`}
                                data-value="inwork"
                            >
                                В работе
                            </a>
                            <a
                                onClick={changeStatus}
                                href="#"
                                className={`btn btn-light ${filterStatus === 'complete' ? 'active' : ''}`}
                                data-value="complete"
                            >
                                Завершенные
                            </a>
                        </div>
                    </div>
                    <div className="col">
                        <select
                            onChange={chooseProduct}
                            className="custom-select"
                            id="productSelect"
                            defaultValue="all"
                        >
                            <option value="all">Все продукты</option>
                            <option value="product-html">Курс по верстке</option>
                            <option value="product-js">Курс по JavaScript</option>
                            <option value="product-vue">Курс по VUE JS</option>
                            <option value="product-php">Курс по PHP</option>
                            <option value="product-wordpress">Курс по WordPress</option>
                        </select>
                    </div>
                </div>
            </div>
        </form>
    );
};
 
export default TableStatus;